import imagesLoaded from './imagesLoaded';
export function hide(item) {
  if (item instanceof HTMLElement) {
    item.style.display = 'none';
    return true;
  }

  return false;
}
export function hideAll(items) {
  if (Array.isArray(items) && items.length) {
    var bad = 0;
    items.forEach(function (item) {
      if (!hide(item)) {
        bad++;
      }
    });
    return bad === 0;
  } else {
    return hide(items);
  }
} // Current version based on DOMConteneLoaded and imagesLoaded
// lets init the calculation when scripts and JS styles
// were applied and images are loaded,
// but probably before final load event, which is better.

export function onContentTrueLoad(container, imageSelector, successCallback, failureCallback) {
  window.addEventListener('DOMContentLoaded', function () {
    var imgNodes = container.querySelectorAll('img.' + imageSelector);
    imagesLoaded(imgNodes).then(successCallback).catch(function (e) {
      if (typeof failureCallback === 'function') {
        failureCallback(e);
      } else {
        console.error(e);
      }
    });
  });
}
export function px(val) {
  return val + 'px';
}
export function getWidthByHeight(dim, ratio) {
  return dim * ratio;
}
export function getHeightByWidth(dim, ratio) {
  return dim / ratio;
}